//
// Scaffolding
// --------------------------------------------------

// Body reset
html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: @font-family-base;
  font-size: @font-size-small;
  line-height: @line-height-base;
  color: @text-color;
  background-color: @body-bg;
}
// Links

a {
  color: @link-color;
  text-decoration: @link-decoration;

  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: @link-hover-decoration;
  }

  &:focus {
    .tab-focus();
  }
}

/* from kuma-base.css */
/* kuma.base  */

/*  */

/*  */
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0;
}

/*  HTML5 , IE  js  createElement(TAG) */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

/* HTML5  img  */
audio, canvas, video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

input, select, textarea {
  font-size: 100%;
}

/* Table  cell  */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* IE bug fixed: th  text-align*/
th {
  text-align: inherit;
}

/*  */
fieldset, img {
  border: 0;
}

//Image align
img {
  vertical-align: middle;
}

/* ie6 7 8(q) bug  */
iframe {
  display: block;
}

/*  firefox  */
abbr, acronym {
  border: 0;
  font-variant: normal;
}

/*  del  */
del {
  text-decoration: line-through;
}

address, caption, cite, code, dfn, em, th, var {
  font-style: normal;
  font-weight: 500;
}

/* , li  */
ol, ul {
  list-style: none;
}

/* ,  */
caption, th {
  text-align: left;
}

/* yahoo, ,  */
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 500;
}

q:before, q:after {
  content: '';
}

/*  */
sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

/*  */
ins, a {
  text-decoration: none;
}

/*  */
code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

/*  */
.fn-clear:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.fn-clear {
  zoom: 1; /* for IE6 IE7 */
}

/* ,  JS  */
.fn-hide {
  display: none;
}

.fn-left {
  float: left;
}

.fn-right {
  float: right;
}

/*  */
.fn-text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* chrome  */
.fn-webkit-adjust {
  -webkit-text-size-adjust: none;
}

/*  */
.fn-highlight {
  color: @orange-dark;
}

/*  */
.fn-comment {
  color: @black-light;
}

// Set Box Sizing to Border Box
*, *:after, *:before {
    .box-sizing(border-box);
}

.fn-content-box {
  .box-sizing(content-box);
  * {
    .box-sizing(inherit);
  }
}

.fn-border-box {
  .box-sizing(border-box);
  * {
    .box-sizing(inherit);
  }
}
